.profile {
  padding-top: 40px;
  display: flex;
  align-items: center;
  gap: 30px;
}

@media screen and (max-width: 768px) {
  .profile {
    flex-direction: column;
    justify-content: center;
  }
}


@media screen and (max-width: 425px) {
  .profile {
    padding-top: 42px;
    gap: 0;
  }
}
